.indexPage {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  box-sizing: border-box;
}

.topDiv {
  flex: 1;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.imageBox {
  width: 100%;
  max-width: 800px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.imageBox img {
  width: 100%;
  height: auto;
  max-height: 100%;
  object-fit: contain;
  margin-top: 20px;
}

.title {
  font-family: "Kaisei Decol", serif;
  font-size: 10.5vw;
  color: #363232;
  letter-spacing: -3px;
  position: absolute;
  text-align: center;
  z-index: 1;
}

.bottomDiv {
  width: 75%;
  font-family: "Kaisei Decol", serif;
  font-size: 1.2rem;
  color: #363232;
  text-align: center;
  padding-bottom: 40px;
  z-index: 2;
}

.bubbleImage {
  width: 10rem;
  position: absolute;
  top: 10%;
  right: 10%;
  animation: float 7s infinite ease-in-out alternate;
  opacity: 0.8;
}

@keyframes float {
  0% {
    transform: translateY(0) translateX(0) rotate(0deg);
  }
  20% {
    transform: translateY(-20px) translateX(-10px) rotate(5deg);
  }
  40% {
    transform: translateY(-40px) translateX(10px) rotate(-5deg);
  }
  60% {
    transform: translateY(-30px) translateX(5px) rotate(3deg);
  }
  80% {
    transform: translateY(-10px) translateX(-5px) rotate(-3deg);
  }
  100% {
    transform: translateY(-60px) translateX(0) rotate(0deg);
  }
}


@media (max-width: 1024px) {
  .imageBox {
    max-width: 100%;
  }

  .imageBox img {
    max-height: 90%;
  }

  .title {
    font-size: 17vw;
  }

  .bubbleImage {
    width: 12rem;
    top: 15%;
    right: 8%;
  }

  .bottomDiv {
    font-size: 1.2rem;
    padding-bottom: 48px;
  }
}

@media (max-width: 768px) {
  .imageBox {
    max-width: 100%;
  }

  .imageBox img {
    max-height: 95%;
  }

  .title {
    font-size: 17vw;
  }

  .bubbleImage {
    width: 8rem;
    top: 20%;
    right: 5%;
  }

  .bottomDiv {
    font-size: 0.9rem;
    padding-bottom: 40px;
  }
}

@media (max-width: 480px) {
  .imageBox {
    max-width: 100%;
  }

  .imageBox img {
    max-height: 100%;
  }

  .title {
    font-size: 17vw;
  }

  .bubbleImage {
    width: 6rem;
    top: 25%;
    right: 5%;
  }

  .bottomDiv {
    font-size: 0.7rem;
    padding-bottom: 40px;
    width: 100%;
  }
}
